.product-cart {
  position: absolute;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  height: 100vh;
  z-index: 8;
  display: none;
  justify-content: center;
  align-items: flex-start;
}
.product-cart.cart-visible {
  display: flex;
}
@media (min-width: 71.875em) {
  .product-cart {
    width: 100%;
    justify-content: flex-end;
  }
}

.product-cart__content {
  width: 93%;
  top: 0.625rem;
  position: relative;
  box-shadow: 0.5625rem 1.5625rem 2.125rem 0.0625rem rgba(0, 0, 0, 0.3);
  border-radius: 0.625rem;
  z-index: 8;
  background: hsl(0deg, 0%, 100%);
  display: flex;
  flex-direction: column;
  padding: 1.5625rem;
}
@media (min-width: 71.875em) {
  .product-cart__content {
    max-width: 25rem;
    top: -0.625rem;
    right: 1.25rem;
  }
}
.product-cart__content h4 {
  padding: 0.625rem 1.25rem 1.5625rem;
  margin: 0;
  color: hsl(220deg, 13%, 13%);
  border-bottom: 0.0625rem solid hsla(220deg, 14%, 75%, 0.4);
}
@media (min-width: 71.875em) {
  .product-cart__content h4 {
    padding: 1.5625rem;
  }
}
.product-cart__content span {
  display: flex;
  width: 100%;
  height: 100%;
  min-height: 6.25rem;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: hsl(219deg, 9%, 45%);
}
.product-cart__content ul.product-cart__content-list {
  flex-grow: 1;
  height: 100%;
  max-height: 25rem;
  overflow: auto;
}
.product-cart__content ul.product-cart__content-list li {
  padding: 1.25rem 0;
  display: flex;
  width: 100%;
  color: hsl(219deg, 9%, 45%);
  align-items: start;
}
.product-cart__content .product-cart__content-img {
  width: 5rem;
}
.product-cart__content .product-cart__content-img img {
  width: 100%;
  border-radius: 0.625rem;
}
.product-cart__content .product-cart__content-desc {
  display: flex;
  flex-direction: column;
  padding: 0.3125rem 0rem;
  margin-left: 5%;
  width: 90%;
}
.product-cart__content .product-cart__content-desc b {
  color: hsl(220deg, 13%, 13%);
}
.product-cart__content .product-cart__content-desc .product-cart__content-desc-title {
  width: 90%;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0;
  min-height: auto;
}
.product-cart__content .product-cart__content-delete {
  transition: all 0.3s ease-in-out;
  padding-top: 0.625rem;
  padding-right: 1.25rem;
}
.product-cart__content .product-cart__content-delete:hover #cart-delete path, .product-cart__content .product-cart__content-delete:active #cart-delete path {
  fill: hsl(26deg, 100%, 55%);
}